@use './assets/colors.scss';

*,
*::before,
*::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	font-size: 62.5%;
}

body,
input,
input::placeholder {
	font-family: 'Rubik', sans-serif;
	font-size: 1.8rem;
}

body,
input {
	color: colors.$very-dark-gray;
}

#map {
	height: 180px;
}
