.map {
	width: 100%;
	height: calc(100vh - 280.78px);
	z-index: 0;
}

.leaflet-popup {
    font-size: 1.5rem;
}

@media (min-width: 768px) {
    .map {
        height: calc(100vh - 293.53px);
    }
}

@media (min-width: 992px) {
    map {
        height: calc(100vh - 293.59px);
    }

}